.site-footer {
    margin-bottom: 12px;
    margin-top: 50px;
    text-align: center;
    padding-top: 54px;
    background: url(../img/top-decor.png) top center no-repeat;

    .info {
        font-size: 15px;
        line-height: 22px;
        color: #495057;

        >* {
            margin-top: 8px;

            &:first-child {
                margin-top: 0;
            }
        }

        .logo {
            background: url(../img/logo.png) center center/198px no-repeat;
            width: 198px;
            height: 50px;
            display: block;
            margin: 0 auto 12px;
        }


    }

    .bold {
        font-weight: $font-weight-semibold;
    }

    .row {
        padding-top: 18px;
        margin-top: 12px;
        background: url(../img/bottom-decor.png) top center no-repeat;

        .text {
            font-size: 15px;
            line-height: 22px;
            text-align: center;
            text-transform: uppercase;
            color: #6C757D;

        }
    }
}